       <h2><span>Основные проблемы:</span></h2>
      <p>В основном, журналисты отмечают, что ситуация с доступом к информации с введением поправок ухудшилась, «несмотря на громкие популистские заявления о "слышащем государстве".</p>

      <p>Разделение информации на официальные сообщения и другие не принесло ничего, кроме путаницы в определениях, сроках и типах информации. Многие журналисты отметили, что чиновники стали использовать эту норму в свою пользу – как аргумент для сокрытия информации. «Государственные органы стали более закрытыми, осторожными» - отмечают респонденты.  «Чиновники стали очень чувствительные – не хотят общаться, а потом журналист думает – лучше буду с ним дружить, чем ссориться, чтобы он хоть что-то дал». </p>

      <p>Зачастую представители госорганов мало знают о законе о СМИ и действующем медиа-законодательстве. Журналисты говорят, что к запросам стали относится более безответственно. Практически все отмечают, что лучше всего работают «личные связи и контакты», альтернативные источники. </p>
      
      <p>«Ругаться бесполезно, нам нужная оперативная информация, поэтому просто подключаем собственные связи» - говорят они. Многие уверены, что доступ к информации – это вопрос профессионализма каждого журналиста. А профессионализм заключается в мастерстве установить личные связи.</p>
      
      <p>Учитывая сложный период в связи с распространением COVID19 и всплеском заболеваемости вирусной пневмонией, журналистам стало сложнее контактировать с компетентными органами, брифинги проходят в онлайн-режиме, не на все вопросы специалисты дают исчерпывающие ответы, а сведения о точном количестве заболевших и умерших от коронавируса не разглашаются. </p>
      
      <p>Главные трудности:</p>
      <ul>
      <li>– увеличение сроков предоставления информации, нарушение сроков,</li>
      <li>– отсутствие адекватных и информативных ответов, </li>
      <li>– требование формального запроса даже в тех случаях, когда нужен быстрый короткий ответ, по существу. </li>
      <li>– увеличение количества этапов согласования, ужесточение внутренних правил в госорганах, по которым предоставляют информацию журналистам.</li> 
      <li>– полное игнорирование запросов,</li>
