      Бывают ситуации, когда республиканским СМИ информацию дают сначала, а региональные публикуют после. Журналисты считают это некорректным, поскольку информация о событии должна появляться там, где это произошло. </p>
      
      <p>Также журналисты отмечают, что госорганы регулярно нарушают сроки, забывают про два дня, которые обозначены в законе. Иногда приходится писать повторно, используя все средства связи: социальные сети, запросы, телефонные звонки. Нередко просят предоставить материал на согласование перед публикацией. </p>
      
      <p>Многие общаются только через пресс-службу, особенно по вопросам, связанным с финансами (акимат ЮКО), требуют материалы на согласование. Что касается профессионализма пресс-служб, то там, где работают бывшие журналисты, дела идут неплохо. У кого в штате их нет вообще, информацию получить очень сложно, потому что сотрудники вынуждены выполнять не свои функции и обязанности. Текучесть в пресс-службах очень высокая. 
