      <p>Многие отмечают закрытость местных правоохранительных органов. Например, информацию о криминальном событии часто получают из МВД, при этом местные ДВД, прокуратура, антикоррупционное ведомство – молчат. Ключевая проблема – необходимость для ДВД согласовывать с прокуратурой любую информацию для СМИ. Это усложняет процесс, увеличивает время, в итоге журналисты не получают нужную им информацию. «Некоторые не берут трубку, заносят номера телефонов в черный список». </p>

