      <p>Казахоязычные журналисты отметили, что на местах созданы региональные службы коммуникации, их материалы тиражируются, журналисты привыкли к готовым текстам. Это неправильно и развращает журналистов.</p> 

      <p>Не все журналисты пользуются системой егов, особенно работающие в региональных газетах. У многих нет ЭЦП, и они не видят смысла в том, чтобы использовать ее для получения информации. В некоторых регионах журналисты отмечают, что ватсапп – главный способ связи. Также нередко связываются с чиновниками через инстаграм и фб. </p>

      <p>Многие отмечают, что сайты государственных органов обновляются крайне редко. </p>

      <h2><span>Как журналисты решают проблемы</span></h2>
      <p>Большинство журналистов предпочитают не реагировать на отказы – не хватает времени, сильная загруженность, не хотят волокиты, не верят, что это может принести результат. Таким образом, очень редко пишут жалобы, практически никогда не обращаются с заявлениями в суды. </p>
      
      <p>Одним из способов добыть информацию является своего рода шантаж, когда журналисты говорят, что напишут в материале о непредоставлении информации либо укажут личные контакты человека, который отказался ее предоставлять. Также журналисты обращают внимание чиновников на то, что информация уже появилась в социальных сетях, поэтому смысла скрывать ее нет. </p>

      <p>Многие решают вопросы через официальные станицы в ФБ, где можно отметить начальника, и тогда дело сдвигается с мертвой точки. </p>
      
      <p>Самая чувствительная информация: финансовая, по неосвоению средств, тендеры, строительство, проверки тендеров, государственный аудит, недропользования, информация из маслихата, инспекция труда, экологическая, религиозная (из КНБ).</p>

      <p>Некоторые отмечали, что «если есть авторитет, если ты ведешь себя спокойно, соблюдаешь правила игры – то информацию дают в срок и по закону».</p> 
      
      <h2><span>Что делать?</span></h2>

      <p>Обязать чиновников работать, ввести административное наказание за отсутствие ответа и неполный ответ, чтобы была ответственность. 
