ывать ее нет. ")]),_c('p',[_vm._v("Многие решают вопросы через официальные станицы в ФБ, где можно отметить начальника, и тогда дело сдвигается с мертвой точки. ")]),_c('p',[_vm._v("Самая чувствительная информация: финансовая, по неосвоению средств, тендеры, строительство, проверки тендеров, государственный аудит, недропользования, информация из маслихата, инспекция труда, экологическая, религиозная (из КНБ).")]),_c('p',[_vm._v("Некоторые отмечали, что «если есть авторитет, если ты ведешь себя спокойно, соблюдаешь правила игры – то информацию дают в срок и по закону».")]),_c('h2',[_c('span',[_vm._v("Что делать?")])]),_c('p',[_vm._v("Обязать чиновников работать, ввести административное наказание за отсутствие ответа и неполный ответ, чтобы была ответственность. Разработать должностные инструкции и правила работы госорганов, где будут прописаны все нормы по работе с информацией. Обязать отвечать по существу, сократить сроки, исключить отписки и возможность относить информацию к секретной. Законодательно закрепить обязанность пресс-секретарей работать в социальных сетях. ")])])}]

